import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router'
import Reply from '../../Components/Modal/Modal';
import axios from 'axios';
import arrowIcon from './arrow.png'
import moment from 'moment';
import './styles.css'
import { useAuth0 } from "@auth0/auth0-react"

const DetailedProject = () => {
  const params = useParams();
  const [project, setProject] = useState({});
  const [comment, setComment] = useState("")
  const [comments, setComments] = useState([])
  const [posts, setPosts] = useState([])
  const { isAuthenticated, getAccessTokenSilently, user } = useAuth0()

  useEffect(() => {
    axios.get(`/get-project/${params.id}`)
      .then(res => {
        setProject(res.data.project)
        setComments(res.data.comments)
        setPosts(res.data.posts)
      })
      .catch(err => {
        console.log(err)
        alert("Their is no project associated with this id.")
        window.location.href = "/projects";
      })
  }, [params.id])

  const handleComment = async (e) => {
    e.preventDefault();

    if (!isAuthenticated) {
      alert("Please login in order to comment.");
      return
    }
    if (!comment) {
      alert("Please enter any message inorder to post a new comment.");
      return
    }

    const token = await getAccessTokenSilently();
    const data = { username: user.nickname, profile: user.picture, hackathon_id: params.id, comment, time: new Date() }

    axios({
      method: "POST",
      url: "/create-new-comment",
      headers: {
        "authorization": `Bearer ${token}`
      },
      data
    }).then(res => {
      const data = res.data.comments
      setComments(data)
      document.getElementById("comment").value = ""
    }).catch(err => {
      console.log(err)
    })
  }

  return (
    <div>
      <section id="blog" className="blog">
        <div className="container-fluid" data-aos="fade-up">

          <div className="row">
            <div className="col-lg-8 entries">
              <article className="entry entry-single">
                <div className="entry-img" align="center">
                  <iframe width="100%" height="550" src={project?.youtube_link} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                  {/* <img width="100%" src="data:image/jpeg;base64," alt="" className="img-fluid"/> */}
                </div>

                <h2 className="entry-title">
                  {project?.hackathon_name}
                </h2>

                <div className="entry-meta">
                  <ul>
                    <li className="d-flex align-items-center">
                      <img style={{ height: "30px", margin: "10px", borderRadius: "15px" }} src={project?.profileImage} alt="profile" />
                      {project?.username}
                    </li>
                    <li className="d-flex align-items-center"><i className="bi bi-person"></i> {project?.theme}</li>
                    <li className="d-flex align-items-center"><i className="bi bi-chat-dots"></i> {comments.length} Comments</li>
                  </ul>
                </div>

                <div className="entry-content">
                  <blockquote>
                    <p>
                      <b>CODE:</b>
                      <a style={{ fontSize: "18px" }} href={project?.repo_link} rel="noreferrer" target="_blank">{project?.repo_link}</a>
                    </p>
                    <br />
                    <p><b>THEME:</b> {project?.stack}</p>
                    <br />
                    <p><b>TEAM MEMBERS</b></p>
                    <ol>
                      {project?.team?.map((team, index) => {
                        return (
                          <a key={index} style={{ textTransform: "capitalize", textDecoration: "none", color: "black" }}
                            href={team.linkedinUrl} target="_blank" rel="noreferrer">
                            <li >{team.name}</li>
                          </a>
                        )
                      })}
                    </ol>
                  </blockquote>
                  <b>Project Detailed Explanation</b>
                  <p>
                    {project?.detailed_desc}
                  </p>
                  <img src="assets/img/blog/blog-inside-post.jpg" className="img-fluid" alt="" />
                </div>
              </article>

              <div className="sidebar col-12 w-100">
                <h3 className="sidebar-title">Related Projects</h3>
                <div className="sidebar-item recent-posts">
                  {posts.map((data, index) => {
                    return (
                      <div key={index} className="post-item clearfix row" style={{ marginBottom: "10px" }}>
                        <div className="col-4">
                          <a href={`/projects/${data._id}`}><img src={data?.cover_pic?.url} alt="Cover pic" /></a>
                          <h4>{data.hackathon_name}</h4>
                          <time dateTime="2020-01-01">{moment(data?.upload_time).fromNow()}</time>
                        </div>
                        <div className="col-8">
                          <p>{data?.detailed_desc.substr(0, 250)}....<a href={`/projects/${data._id}`}>View more</a></p>
                        </div>
                        <br />
                      </div>
                    )
                  })}
                </div>
              </div>
            </div>

            <div className="col-lg-4">
              <div className="blog-comments">
                <div className="reply-form">
                  <h4>Leave a Reply</h4>
                  <p>Your email address will not be published. Required fields are marked * </p>
                  <form onSubmit={handleComment}>
                    <div className="row">
                      <div className="col form-group">
                        <textarea name="comment" className="form-control" id="comment" placeholder="Your Comment*"
                          onChange={(e) => setComment(e.target.value)} required>
                        </textarea>
                      </div>
                    </div>
                    <button type="submit" className="btn btn-primary">Post Comment</button>
                  </form>

                  <br />
                  <h4 className="comments-count">{comments.length} Comments</h4>

                  {comments?.map((data, index) => {
                    return (
                      <div key={index}>
                        <div id="comment-1" className="comment">
                          <div className="d-flex">
                            <div className="comment-img">
                              <img style={{ width: "40px", borderRadius: "50%", marginTop: "5px", border: "2px solid grey" }}
                                src={data.profile} alt="profile" />
                            </div>
                            <div>
                              <h5>
                                {data.username}
                                <i className="bi bi-reply-fill"></i>
                                <Reply
                                  index={index}
                                  projectId={params.id}
                                  commentId={data._id}
                                  reply="Reply"
                                  replyTo={data.username}
                                  setComments={setComments}
                                />
                              </h5>
                              <time dateTime="2020-01-01">{moment(data.time).fromNow()}</time>
                              <p> {data.comment} </p>
                            </div>
                          </div>
                        </div>
                        {data.subComments.length > 0 && (
                          <p>
                            <b>
                              <a style={{ color: "black", textDecoration: "none", marginLeft: "20PX" }}
                                data-toggle="collapse" href={"#multiCollapseExample" + String(index)}
                                role="button" aria-expanded="false" aria-controls={"multiCollapseExample" + String(index)}>
                                {data.subComments.length} replies  <img src={arrowIcon} height="12px" alt="" />
                              </a>
                            </b>
                          </p>
                        )}

                        <div className="collapse multi-collapse" id={"multiCollapseExample" + String(index)}>
                          {data.subComments.map((sub, index) => {
                            return (
                              <div>
                                <div key={index} id="comment-reply-1" className="comment comment-reply" >
                                  <div className="d-flex" style={{ overflowX: "auto", width: "100%" }}>
                                    <div className="comment-img">
                                      <img style={{ width: "40px", borderRadius: "50%", marginTop: "5px", border: "2px solid grey" }}
                                        src={sub.profile} alt="profile" />
                                    </div>
                                    <div>
                                      <h5>{sub.username}</h5>
                                      <time dateTime="2020-01-01">{moment(sub.time).fromNow()}</time>
                                      <p> {sub.comment} </p>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            )
                          })}
                        </div>
                      </div>
                    )
                  })}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}

export default DetailedProject
